@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Esteban&display=swap);

.bttn:hover {
  color:#0a192f;
}
.bttn:focus {
  color:#0a192f;
  outline: 0 !important;
} 


.bttn {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  padding:10px 50px;
  letter-spacing: 1.5px;
  border-radius: 25px;
  border: 1px solid #e2cfd7;
  cursor: "pointer";
  font-family:'Montserrat';
  background: transparent;
  color:#B5B9E4; 
  position: relative;
}

.bttn:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: #e2cfd7;
  z-index: -2;
  border-radius: 25px;
}

.bttn:hover:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
}

.bttn:focus:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;

}


	@-webkit-keyframes scroll {
	0% {
	-webkit-transform: translateX(0);
	transform: translateX(0);
	}
	100% {
	-webkit-transform: translateX(calc(-300px * 7));
	transform: translateX(calc(-300px * 7));
	}
	}


	@keyframes scroll {
	0% {
	-webkit-transform: translateX(0);
	transform: translateX(0);
	}
	100% {
	 -webkit-transform: translateX(calc(-300px * 7));
	transform: translateX(calc(-300px * 7)); 
	}
	}

	
	.slider {
	background: transparent;
	height: 200px;
	margin: auto;
	overflow: hidden;
	position: relative;
	width: 100%;
	}

	.slider::before, .slider::after {
	content: "";
	height: 200px;
	position: absolute;
	z-index: 2;
	}

	.slider::after {
	right: 0;
	top: 0;
	-webkit-transform: rotateZ(180deg);
	transform: rotateZ(180deg);
	}

	.slider::before {
	left: 0;
	top: 0;
	}

	.slider .slide-track {
	 -webkit-animation: scroll 20s linear infinite; 
	 animation: scroll 20s linear infinite; 
	 display: flex;
	 width: calc(300px * 10);
	}

	.slider .slide {
	height: 2500px;
	width: 300px;
	} 


	
	  
